.Toolbar{
    padding: 0 20px;
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: var(--primary-dark);
    /* #703B09 */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav{
    height: 100%;
}

@media (max-width: 499px){
    .desktopNav {
        display: none;
    }
}

.Hamburger {
    width: 40px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
}

.Hamburger div {
    width: 90%;
    height: 3px;
    background-color: white;
}

@media (min-width: 500px) {
    .Hamburger {
        display: none;
    }
}


