.BuildControls{    
    margin: auto;
    padding: 10px 0;
    width: 100%;
    background-color: var(--primary-light);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.OrderButton {
    padding: 1rem 0;
    width: 280px;
    background-color: var(--primary);
    outline: none;
    border: none;
    cursor: pointer;
    color: black;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
}

.OrderButton:active {
    background-color: var(--accent-light);
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    color: #888888;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


